itle"},[_vm._v("阅读须知：")]),_c('p',[_vm._v("①本报告基于您的在线作答结果，有针对性地为您提供建议。")]),_c('p',[_vm._v("②测评结果的准确性和可靠性主要依赖于您是否对自己有明确客观的认识，以及在测评过程中是否认真和坦白等。")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"page_shadow"},[_c('div',{staticClass:"page pdf-page"},[_c('h5',{staticClass:"header"},[_vm._v("\n                    第三部分 温馨提示\n                ")]),_c('div',{staticClass:"content warmtips",attrs:{"id":"catalog_3"}},[_c('p',[_vm._v("注意的稳定性直接影响学习效率，进而影响中学生的学习成绩，因此对中学生进行注意力训练是非常有必要的，以下是注意力训练的几种方法，可以独立进行训练：")]),_c('div',{staticClass:"tips_box"},[_c('h6',{staticClass:"tips_title"},[_vm._v("1.142呼吸法：")]),_c('p',[_vm._v("闭上自己的眼睛，在闭上眼睛的同时请检查一下你是否保持了科学坐姿，如果是就在心里表扬一下你自己，如果不是请调整你的坐姿。深深地吸一口气，感觉小肚子鼓起来了，又鼓起来了，慢慢地吐气，你的小肚子在一点一点的变小，把你肚子中所有不快乐的气体统统呼出，现在的你放松极了，慢慢地吸进一口清凉的空气，感觉你的心情变得非常轻松，非常愉悦，你的小肚子慢慢地鼓起来了，然后将你体内所有的不快，所有的烦恼慢慢地呼出，你的身体凝聚一股神奇的力量，请在心里告诉自己：我已经做好上课的准备了，我要开始上课了，默数2个数字，睁开你的眼睛。")]),_c('p',[_vm._v("训练过程中，呼吸的深度是按照吸气1秒钟，屏住呼吸4秒钟，吐气2秒钟来进行，可以自己在心里数节拍，自行控制节奏。如果不好掌控学生呼吸的深度，可以配合手势来进行。即：一手放在自己的脖子下面，另一只手放在自己的小肚子上，随着深呼吸的进行，你会感到下面的手在一吸一呼的过程中慢慢地前后移动着，而上面的这只手并没太大频率的移动即可。")])]),_c('div',{staticClass:"tips_box"},[_c('h6',{staticClass:"tips_title"},[_vm._v("1.冥想法：")]),_c('p',[_vm._v("身体坐直，全身放松，双手自然放在两腿上，手心向内，微闭双目，全身放松，慢慢地深呼吸。一边深呼吸，一边想象在一片大山的深处有一潭平静的湖水，湖面非常平静，没有一丝波纹，湖边长满了花草树木.....（一幅有情景的文章）。结束后，进行自我暗示，比如我发觉我的注意力越来越集中了，我的心情也越来越好了等等。")]),_c('p',[_vm._v("冥想训练强调的是在安静的环境中进行的行为训练，在进行的过程要与自己的呼吸相结合，呼吸要求吸气像品花香一样慢慢地吸气，吐气像蚕吐丝一样微微的吐气不发出任何声音。")])])])])])}]

